.r-scrollbar{overflow:hidden;position:relative}.r-scrollbar .r-scrollbar__wrap{height:100%;overflow:scroll;overflow-x:auto}.r-scrollbar .r-scrollbar__bar{position:absolute;right:2px;bottom:0;z-index:1;border-radius:4px;opacity:0;transition:opacity .12s ease-out}.r-scrollbar .r-scrollbar__bar.is-horizontal{height:6px;left:2px}.r-scrollbar .r-scrollbar__bar.is-horizontal>div{height:100%}.r-scrollbar .r-scrollbar__bar.is-vertical{width:6px;top:0}.r-scrollbar .r-scrollbar__bar.is-vertical>div{width:100%}.r-scrollbar .r-scrollbar__bar .r-scrollbar__thumb{position:relative;display:block;border-radius:4px;background-color:rgba(144,147,153,0.3);transition:background-color .3s}.r-scrollbar .r-scrollbar__bar.r-scrollbar__bar__show{opacity:1}.r-scrollbar .r-scrollbar__bar.r-scrollbar__bar__show .r-scrollbar__thumb{background-color:rgba(144,147,153,0.5)}
