ass{color:#e96900}.ra-docs-wrapper .css .hljs-number,.ra-docs-wrapper .javascript .hljs-number{color:#fc1e70}.ra-docs-wrapper .css .hljs-attribute{color:#af7dff}.ra-docs-wrapper .css .hljs-important{color:red}.ra-docs-wrapper .actionscript .hljs-literal,.ra-docs-wrapper .javascript .hljs-literal{color:#fc1e70}.ra-docs-wrapper pre{padding:0;margin:0}.ra-docs-wrapper code{padding:.2em .4em;margin:0;font-size:85%;background-color:#fff5f5;border-radius:3px;font-family:SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier, monospace;color:#fa795e}.ra-docs-wrapper pre code{display:inline;margin:0;padding:0;border:none;background:transparent}.ra-docs-wrapper pre code{background:#f7f7f7}.ra-docs-wrapper table{font-family:Consolas, Menlo, Courier, monospace;font-size:12px;border-collapse:collapse;border-spacing:0;empty-cells:show;border:1px solid #e9e9e9;width:100%;margin-bottom:24px}.ra-docs-wrapper table th{background:#f7f7f7;white-space:nowrap;color:#5c6b77;font-weight:600}.ra-docs-wrapper table td,.ra-docs-wrapper table th{border:1px solid #e9e9e9;padding:8px 16px;text-align:left}.ra-docs-wrapper table td ul li{font-size:12px !important}
